<template>
  <div class="login-container">
    <van-nav-bar class="page-nav" title="登录">
      <template #left>
        <van-icon name="cross" @click="$router.back()"></van-icon>
      </template>
    </van-nav-bar>
    <van-form @submit="onSubmit" ref="loginForm">
      <van-field
        v-model="user.mobile"
        name="mobile"
        label="手机号"
        placeholder="手机号"
        :rules="userFormRules.mobile"
      >
        <template #left-icon>
          <i class="toutiao toutiao-shouji"></i>
        </template>
      </van-field>

      <van-field
        v-model="user.code"
        type="password"
        name="code"
        label="密码"
        placeholder="密码"
        :rules="userFormRules.code"
      >
        <template #left-icon>
          <i class="toutiao toutiao-yanzhengma"></i>
        </template>
        <template #button>
          <van-count-down
            :time="1000 * 6"
            v-if="isCountDownShow"
            format="ss 秒"
            @finish="isCountDownShow = false"
          />
          <van-button
            class="send-sms-btn"
            size="small"
            type="default"
            native-type="button"
            round
            @click="onSendSms()"
            v-else
            >发送验证码</van-button
          >
        </template>
      </van-field>
      <div class="login-btn-wrap">
        <van-button class="login-btn" block type="info" native-type="submit"
          >登录</van-button
        >
      </div>
    </van-form>
  </div>
</template>
<script>

import { loginAPI, getSmsCodeAPI } from '../../api/index.js'
export default {
  name: 'LoginPage',
  data () {
    return {
      user: {
        mobile: '',
        code: ''
      },
      isCountDownShow: false,
      time: '',
      userFormRules: {
        mobile: [{
          required: true,
          message: '手机号不能为空'
        }, {
          pattern: /^1[3|5|7|8]\d{9}$/,
          message: '手机格式错误'
        }],
        code: [{
          required: true,
          message: '验证码不能为空'
        }, {
          pattern: /^\d{6}$/,
          message: '验证码格式错误'
        }]
      }
    }
  },
  methods: {
    async onSendSms () {
      try {
        await this.$refs.loginForm.validate('mobile')
        this.isCountDownShow = true
      } catch (error) {
        return console.log('验证失败', error)
      } try {
        await getSmsCodeAPI(this.user.mobile)
        this.$toast('发送成功')
      } catch (error) {
        this.isCountDownShow = false
        if (error.response.status === 429) {
          this.$toast.fail('发送频繁,请稍后再试')
        } else {
          this.$toast.fail('发送失败')
        }
      }
    },
    async onSubmit () {
      const toast = this.$toast.loading({
        duration: 0,
        message: '登录中...',
        forbidClick: true
      })
      console.log('submit', this.user)
      try {
        const res = await loginAPI({
          mobile: this.user.mobile,
          code: this.user.code
        })
        console.log('登录成功', res)
        this.$toast.success('登录成功')
        this.$store.commit('setUser', res.data.data)
        this.$router.back()
      } catch (error) {
        toast.clear()
        if (error.response.status === 400) {
          console.log('手机或验证码错误', error)
          this.$toast.fail('登录失败，手机号或验证码错误')
        } else {
          console.log('系统错误', error)
          this.$toast.fail('系统错误')
        }
      }
    }
  }
}
</script>
<style scope lang="less">
.login-container {
  .toutiao {
    font-size: 32px;
  }
  .login-btn-wrap {
    padding: 53px 53px;
    .login-btn {
      background-color: #6db4fb;
      border: none;
    }
  }
}
.page-nav {
  .van-icon {
    color: #fff;
  }
}
</style>
